@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensans/opensans-300.woff2") format("woff2"), url("../fonts/opensans/opensans-300.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans/opensans-400.woff2") format("woff2"), url("../fonts/opensans/opensans-400.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/opensans/opensans-600.woff2") format("woff2"), url("../fonts/opensans/opensans-600.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensans/opensans-700.woff2") format("woff2"), url("../fonts/opensans/opensans-700.woff") format("woff"); }

/* -- ОБЩИЕ -- */
*,
*::after,
*::before {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  letter-spacing: -0.005em;
  background-color: #ffffff; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0; }

.nowrap {
  white-space: nowrap; }

/* -- CONTAINER -- */
.container {
  max-width: 1200px;
  padding: 0 30px;
  margin: 0 auto;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .container {
      max-width: 100%;
      padding: 0 0; } }

/* -- HEADER -- */
.header {
  display: flex;
  flex-direction: column;
  min-height: 825px;
  padding-bottom: 50px;
  background: #000000 url("../img/header_bg_desktop_1x.jpg") center top no-repeat;
  background-size: cover;
  /* -- RETINA -- */ }
  @media (min-resolution: 1.5dppx) {
    .header {
      background-image: url("../img/header_bg_desktop_2x.jpg"); } }

.header__top {
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__top {
      display: none; } }

.header__top-inner {
  min-height: 65px;
  width: 77.02%;
  min-width: 800px;
  margin: 0 auto; }

.header__nav {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.header__site-nav {
  margin: 0; }

.header__middle {
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__middle {
      order: -1; } }

.header__middle-inner {
  width: 77.02%;
  min-width: 800px;
  margin: 0 auto;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__middle-inner {
      display: flex;
      justify-content: space-between;
      width: 100%;
      min-width: 100%;
      margin: 0; } }

.header-logo {
  position: relative;
  bottom: -252px;
  display: block;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header-logo {
      bottom: 0; } }

.header__burger {
  display: none;
  /* -- TABLET -- */ }
  @media (max-width: 1023px) {
    .header__burger {
      display: block; } }

.header__bottom {
  padding-top: 228px; }

.header__bottom-inner {
  display: flex;
  justify-content: space-between;
  width: 77.02%;
  min-width: 800px;
  margin: 0 auto; }

.header__bottom-left {
  width: 420px;
  padding-top: 53px; }

.header__page-title {
  margin: 0;
  padding-bottom: 24px;
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  color: #ffffff; }

.header__slogan {
  margin: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.8); }

.header__bottom-right {
  width: 327px;
  padding-top: 5px; }

/* -- ГЛАВНАЯ НАВИГАЦИЯ -- */
.site-nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0;
  padding-top: 22px;
  padding-left: 19px;
  padding-right: 22px;
  padding-bottom: 10px;
  margin-left: -20px;
  list-style: none; }

.site-nav li {
  margin-left: 20px; }

.site-nav a {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  letter-spacing: -0.005em;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.7);
  transition: 0.3s; }
  .site-nav a:hover {
    opacity: 0.7; }
  .site-nav a:active {
    opacity: 0.5; }

/* -- Login Form -- */
.login-form__label {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  color: #ffffff;
  text-align: center; }

.login-form input {
  width: 327px;
  height: 48px;
  padding-top: 8px;
  padding-left: 29px;
  padding-right: 29px;
  margin-bottom: 20px;
  font-size: 15px;
  line-height: 24px;
  color: #1c1c1c;
  border: 1px solid #c9ccd4;
  border-radius: 4px;
  background-color: #ffffff; }
  .login-form input:placeholder {
    opacity: 0.5; }

.login-form button[type="submit"] {
  width: 326px;
  padding: 15px 20px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  border-radius: 6px;
  background-color: #0ad9c6; }
