/* -- CONTAINER -- */
.container {
  max-width: $desktop_container_width;
  padding: 0 $desktop_padding;
  margin: 0 auto;

  /* -- TABLET -- */
  @media (max-width: $tablet_max_width) {
    max-width: 100%;
    padding: 0 $tablet_padding;
  }

